import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-property-binding',
  templateUrl: './property-binding.component.html',
  styleUrls: ['./property-binding.component.scss'],
})
export class PropertyBindingComponent implements OnInit {
  public imageUrlBinding: string =
    'data:image/jpeg;base64,';
  public isDisabled: boolean = true;
  public classes: string = 'property-binding__yellow';
  public parentItem: PropertyBindingComponent;
  public title: string = 'This is title';
  public evilTitle =
    'Template <script>alert("evil never sleeps")</script> Syntax';
  constructor() {}

  ngOnInit(): void {
    this.parentItem = this;
  }
  public toggleIsDisabled() {
    this.isDisabled = !this.isDisabled;
  }
}
